export default {
	data() {
		return {
			dev_id: "",
			mer_name: "",
			day_money: "",
			day_amount: "",
			month_money: "",
			month_amount: "",
			start_time: "",
			end_time: "",
			
			mer_id: "",
			emit: 10,
			list: [],  // 存放交易流水
			loadtext: "上拉加载更多",
		}
	},
	onReachBottom() {
		if(this.emit > this.list.length) {
			console.log('不会再上拉了哦');
			return
		}
		console.log('啦啦啦');
		this.loadtext = "加载中..."
		this.emit += 10 
		console.log("触发上拉加载", this.emit);
		// this.__init()
		this.__deal()
	},
	onLoad(option) {
		this.dev_id = option.dev_id
		this.mer_name = option.mer_name
		// 从收益统计页 获取时间
		const value = uni.getStorageSync('earn');
		console.log('value', value);
		if(!value.start_time) {
			uni.showToast({
				title: "请先去'收益统计'确定时间",
				icon: "none",
				duration: 3000
			})
		}
		this.start_time = value.start_time
		this.end_time = this.$Time.getTime()
		console.log("开始时间：", this.start_time, '结束时间：',this.end_time);
		this.__today()
		this.__month()
		// 请求商户ID
		this.__init()
		
		
		
	},
	methods: {
		// 本月 收益统计
		async __month() {
			this.$H.post("/agent/", {
				user_id: uni.getStorageSync('uid'),
				token: uni.getStorageSync('utoken'),
				opt: "agent_device_statistics",
				order_status: "1",
				device_id: this.dev_id,
				start_time: this.start_time,
				end_time: this.end_time,
				slimit: 0,
				elimit: 5,
				group: 'month'
			}).then((res) => {
				this.month_money = res.count[0].sum_money
				this.month_amount = res.count[0].count_num
			}).catch((e) => {
				console.log("catch error:", e);
			})
		},
		// 今天 收益统计
		async __today() {
			this.$H.post("/agent/", {
				user_id: uni.getStorageSync('uid'),
				token: uni.getStorageSync('utoken'),
				opt: "agent_device_statistics",
				order_status: 1,
				device_id: this.dev_id,
				start_time: this.start_time,
				end_time: this.end_time,
				slimit: 0,
				elimit: 5,
				group: 'day'
			}).then((res) => {
				this.day_money = res.count[0].sum_money
				this.day_amount = res.count[0].count_num
			}).catch((e) => {
				console.log("catch error:", e);
			})
		},
		// 请求商户ID
		async __init() {
			this.$H.post("/agent/", {
				user_id: uni.getStorageSync('uid'),
				token: uni.getStorageSync('utoken'),
				opt: "merchant_list",
				key_value: this.mer_name,
			}).then((data) => {
				this.mer_id = data.arr[0].merchant_id
				console.log('商户ID:', this.mer_id);
				// 交易流水
				this.__deal()
			}).catch((e) => {
				console.log("catch error:", e);
			})
		},
		// 交易流水
		async __deal() {
			this.$H.post("/agent/", {
				user_id: uni.getStorageSync('uid'),
				token: uni.getStorageSync('utoken'),
				opt: "order_list",
				merchant_id: this.mer_id,
				// merchant_id: 375,
				order_status: "1",
				start_time: this.start_time,
				end_time: this.end_time,
				slimit: 0,
				elimit: this.emit,
			}).then((res) => {
				this.list = res.arr
				console.log("list数组长度：", this.list.length,'emit长度：',this.emit);
				// 恢复加载状态
				this.loadtext = this.list.length < this.emit ? "没有更多了" :  "上拉加载更多"
			}).catch((e) => {
				console.log("catch error:", e);
			})
		},
		// 去详情页
		toDeviceOrder(item) {
			uni.setStorageSync('DeviceOrder', item);
			uni.navigateTo({
				url: `/pages/device-order/device-order?mer_name=${this.mer_name}`
			})
		},
	}
}
